header {
  background: url("../img/header_img.jpg") center no-repeat;
  background-size: cover;
  width: 100%;
  height: 775px;
}
header .navbar-brand_mini {
  display: none;
}
header .header-navbar_list {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
header .header-navbar_list .header-navbar_list_items {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header-navbar_list .header-navbar_list_items .nav-link {
  font-size: 17px;
  font-weight: 600;
}
header .header-navbar_list .header-navbar_list_items .brand-logo,
header .header-navbar_list .header-navbar_list_items .brand-logo_max992 {
  width: "68";
  height: "74";
}
@media all and (max-width: 992px) {
  header {
    height: 100vh;
  }
  header .header-navbar_list_items li:nth-child(3) {
    display: none;
  }
  header .navbar-brand_mini {
    display: block !important;
  }
}
.photo-preview {
  background: #f3f3f3;
  display: flex;
  justify-content: center;
}
.photo-preview .photo-preview_conteiner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.photo-preview .photo-preview_conteiner .photo-preview_conteiner__text {
  width: 50%;
  font-size: 20px;
  font-family: 'Abhaya Libre', serif;
  margin: 200px 0px;
}
.photo-preview .photo-preview_conteiner .photo-preview_conteiner-img {
  margin-bottom: 100px!important;
}
@media all and (max-width: 1200px) {
  .photo-preview .photo-preview_conteiner .photo-preview_conteiner__text {
    margin: 100px 0px;
  }
  .photo-preview .photo-preview_conteiner-img {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .photo-preview .photo-preview_conteiner-img .photo-preview_conteiner-img_first {
    margin-bottom: 0px!important;
  }
  .photo-preview .photo-preview_conteiner-img .photo-preview_conteiner-img_first img {
    flex: 1;
    max-width: 100%;
  }
  .photo-preview .photo-preview_conteiner-img .photo-preview_conteiner-img_second {
    margin-top: 0px!important;
  }
  .photo-preview .photo-preview_conteiner-img .photo-preview_conteiner-img_second img {
    flex: 1;
    max-width: 100%;
  }
}
.nature {
  background: #41746c;
  height: 380px;
}
.nature .nature_conteiner {
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nature .nature_conteiner .nature_conteiner-line {
  border-left: 2px solid #ffffff;
  height: 300px;
}
.nature .nature_conteiner .nature_conteiner-respect,
.nature .nature_conteiner .nature_conteiner-like {
  font-family: 'Abhaya Libre', serif;
  display: flex;
  flex-direction: column;
}
.nature .nature_conteiner .nature_conteiner-respect h2,
.nature .nature_conteiner .nature_conteiner-like h2 {
  font-size: 20px;
  font-weight: bold;
  color: #d9e3e2;
  text-decoration: underline;
  margin-bottom: 30px;
}
.nature .nature_conteiner .nature_conteiner-respect p,
.nature .nature_conteiner .nature_conteiner-like p {
  font-size: 17px;
  color: #d9e3e2;
  width: 60%;
  align-self: center;
  line-height: 1.2;
}
@media all and (max-width: 1200px) {
  .nature .nature_conteiner {
    width: 100%!important;
  }
}
.above {
  background: #f3f3f3;
  display: flex;
  justify-content: center;
}
.above .above_conteiner {
  font-family: 'Abhaya Libre', serif;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  margin: 200px 0px 200px 0px;
}
.above .above_conteiner .above_conteiner-head {
  align-self: center;
  width: 60%;
  margin: 100px 0px 200px 0px;
}
.above .above_conteiner .above_conteiner-head h2 {
  font-size: 25px;
  color: #31332e;
  font-weight: bold;
}
.above .above_conteiner .above_conteiner-head p {
  width: 60%;
  color: #656663;
  font-size: 19px;
  margin: 0 auto;
}
.above .above_conteiner .above_conteiner_cards {
  align-self: center;
}
.above .above_conteiner .above_conteiner_cards .card {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border: none;
  margin-top: 50px;
  background: #f3f3f3;
}
.above .above_conteiner .above_conteiner_cards .card img {
  width: 627px;
  height: 380px;
}
.above .above_conteiner .above_conteiner_cards .card .card-body {
  width: 300px;
  color: #709690;
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}
.above .above_conteiner .above_conteiner_cards .card .card-body h2 {
  font-size: 21px;
  font-weight: bold;
}
.above .above_conteiner .above_conteiner_cards .card .card-body span {
  font-size: 17px;
}
.above .above_conteiner .above_conteiner_cards .card .card-body p {
  margin-top: 40px;
  font-size: 17px;
  line-height: 1.2;
}
@media all and (max-width: 992px) {
  .above .above_conteiner .above_conteiner-head {
    margin: 10px 0px 20px 0px;
  }
  .above .above_conteiner .above_conteiner-head p {
    width: 100%;
    color: #656663;
    font-size: 19px;
    margin: 0 auto;
  }
  .above .above_conteiner .above_conteiner_cards .card {
    flex-direction: column;
  }
  .above .above_conteiner .above_conteiner_cards .card img {
    width: 80%;
    height: 80%;
  }
}
